:root {
  --window-color:#363636;
  --header-font:"Nunito";
  /*--window-color:#775bff;*/
  scrollbar-width: thin; 

}

::selection {
  background: #775bff; /* WebKit/Blink Browsers */
  color: #fEf;
}
::-moz-selection {
  background: #60ffbc; /* Gecko Browsers */
  color: #000;
}

/*::-webkit-scrollbar {
  display: none;
}
*/

img{
  pointer-events: none;
}

.content::-webkit-scrollbar {
  display:none;
}


.ContentWindowContent::-webkit-scrollbar {
  display:block;
  width:7px;
  /*background-color: #fff;*/
}

.ContentWindowContent::-webkit-scrollbar-thumb {
  background-color: #60ffbc ;
}

.window {
  border: 1px solid;
  padding: 10px;
  box-shadow: 5px 5px;
  max-width: 200px;
}

body{
  background-color: #fff;
  margin: 0px;
  overflow-x: hidden; /* Hide scrollbars */
  overflow-y: hidden;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 20px;
  color: #363636;
  color: var(--window-color);
  /*scrollbar-width: none;*/

}

hr{
  border: none;
  border-top: dotted;
}

a{
  color:hotpink;
  text-decoration: none;
}
 /* unvisited link */
a:link {
  /*color: #fd75ff;*/
  color: #0272ff;
  font-weight: bold;

}

/* visited link */
a:visited {
  color: #02c3ff;

}

/* mouse over link */
a:hover {
  color: #fff;
  background-color: #0272ff;
}

/* selected link */
a:active {
  color: blue;
} 



h2{
  font-family: 'Poiret One', cursive;
  font-size: 32px; 
}


.fullWidthImg{
  width:100%;
}

.gradient {

  /*background: linear-gradient(-60deg, #81f7f7 20%, #ff98be 30%, #ff98be 70%, #81f7f7   80%);*/
  background: linear-gradient(-60deg, #775bff 20%, #fd75ff 30%, #fd75ff 70%, #775bff  80%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
  background-size: 200% auto;
  -webkit-animation: textShine 7s ease-in-out infinite;
          animation: textShine 7s ease-in-out infinite;
}

@-webkit-keyframes textShine {
  to {
    background-position: 200%;
  }
}

@keyframes textShine {
  to {
    background-position: 200%;
  }
}



.header{
  font-family: "Nunito";
  font-family: var(--header-font);
  font-size: 40px;
  font-weight: 500;
}

.content {
  position: fixed;
  background-image: radial-gradient(#fdbdbd 8%,transparent 10%);
  background-size: 15px 15px;
  color: #222;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  scrollbar-color: #60ffbc #fff;
  scrollbar-width: thin;

  /*padding: 20px;*/
}

#LeftWrapper{
