}

.fullbutton{
  /*background-color: #60ffbc;*/
  display: block;
  padding: 20px;
  border-radius: 30px;
  border: 1.5px dotted #333;
  /*background-color: #eee;*/
  overflow: hidden;
}
 
.fullbutton:hover{
  background: linear-gradient(-60deg, #775bff 0%, #fd75ff 25%, #fd75ff 75%, #775bff  100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-fill-color: transparent;
  background-size: 800% auto;
  
  -webkit-animation: textShine 7s ease-in-out infinite;
  
          animation: textShine 7s ease-in-out infinite;

  background-color: #fee;

  cursor: cell;

  }

